<template>
  <div>
    <b-alert
      variant="danger"
      :show="userData === undefined"
    >
      <h4 class="alert-heading">
        {{ errorMessage }}
      </h4>
      <div class="alert-body">
        Ninguna atención encontrada con esta identificación. Verifique la
        <b-link
          class="alert-link"
          :to="{ name: 'salud-atencion-list'}"
        >
          Lista de atenciones
        </b-link>
        para otras atenciones.
      </div>
    </b-alert>

    <section>

      <b-row
        v-if="userData"
        class="match-height"
      >
        <b-col
          lg="12"
          md="12"
          sm="12"
        >
          <b-card no-body>
            <div class="d-flex justify-content-between mt-1">
              <div>
                <!-- Fecha y hora: -->
              </div>
              <div>
                <b-card-title class="mb-0">
                  FORMATO ÚNICO DE ATENCIÓN
                </b-card-title>
                <div class="text-center">
                  <span class="text-danger font-weight-bold"> FUA N° - {{ userData.show_numero_atencion }}</span>
                </div>
              </div>
              <div>
                <b-dropdown
                  variant="transparent"
                  class="chart-dropdown "
                  right
                  no-caret

                  size="sm"
                >

                  <template #button-content>
                    <feather-icon
                      icon="MoreVerticalIcon"
                      size="16"
                      class="align-middle text-body"
                    />
                  </template>
                  <b-dropdown-item>
                    <feather-icon icon="FileTextIcon" />
                    <span class="align-middle ml-50">Ver Historia Clinica</span>
                  </b-dropdown-item>

                  <b-dropdown-item
                    v-if="userData.servicio.nombre === 'Odontología'"
                    @click="generarNuevoOdontograma(idPaciente)"
                  >
                    <feather-icon icon="PlusCircleIcon" />
                    <span class="align-middle ml-50">Odontograma</span>
                  </b-dropdown-item>

                  <b-dropdown-item @click="$bvModal.show('modal_receta')">
                    <feather-icon icon="PlusCircleIcon" />
                    <span class="align-middle ml-50">Receta</span>
                  </b-dropdown-item>

                  <b-dropdown-item @click="$bvModal.show('modal_interconsulta')">
                    <feather-icon icon="PlusCircleIcon" />
                    <span class="align-middle ml-50">Inter consulta</span>
                  </b-dropdown-item>
                </b-dropdown>
              </div>
            </div>
          </b-card>
        </b-col>

        <b-col
          lg="4"
          md="6"
          sm="12"
        >
          <!-- datos del paciente -->
          <b-card
            no-body
          >
            <b-card-header class="pb-0 d-flex justify-content-center">
              <b-card-title>
                <b-badge variant="light-primary">
                  PACIENTE
                </b-badge>
              </b-card-title>
            </b-card-header>

            <b-card-body>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">dni:</span>
                </b-col>
                <b-col cols="7">
                  {{ userData.persona.numero_documento }}
                </b-col>
              </b-row>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">apellidos:</span>
                </b-col>
                <b-col cols="7">
                  {{ userData.persona.apellidos }}
                </b-col>
              </b-row>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">nombres:</span>
                </b-col>
                <b-col cols="7">
                  {{ userData.persona.nombres }}
                </b-col>
              </b-row>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">fecha nac.</span>
                </b-col>
                <b-col cols="7">
                  {{ userData.persona.fecha_nacimiento }}
                </b-col>
              </b-row>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">edad:</span>
                </b-col>
                <b-col cols="7">
                  {{ showEdad }}
                </b-col>
              </b-row>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">sexo:</span>
                </b-col>
                <b-col cols="7">
                  {{ userData.persona.sexo_nombre }}
                </b-col>
              </b-row>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">etnia</span>
                </b-col>
                <b-col cols="7">
                  {{ userData.persona.pueblo_indigena.nombre }}
                </b-col>
              </b-row>

            </b-card-body>
          </b-card>
        </b-col>

        <!-- datos del DATOS DE LA ATENCION ACTIVIDADES PREVENTIVAS Y OTROS -->
        <b-col
          lg="4"
          md="6"
          sm="12"
        >

          <b-card
            no-body
          >
            <b-card-header class="pb-0 d-flex justify-content-center">
              <b-card-title>

                <b-badge variant="light-info">
                  DATOS DE LA ATENCIÓN
                </b-badge>
              </b-card-title>
            </b-card-header>

            <b-card-body>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">n° atención:</span>
                </b-col>
                <b-col cols="7">
                  {{ userData.show_numero_atencion }}
                </b-col>
              </b-row>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">n° h. clinica:</span>
                </b-col>
                <b-col cols="7">
                  {{ userData.persona.numero_documento }}
                </b-col>
              </b-row>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">servicio:</span>
                </b-col>
                <b-col cols="7">
                  {{ userData.servicio.nombre }}
                </b-col>
              </b-row>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">temperatura.</span>
                </b-col>
                <b-col cols="7">
                  {{ userData.triaje.temperatura }} °C
                </b-col>
              </b-row>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">talla:</span>
                </b-col>
                <b-col cols="7">
                  {{ userData.triaje.talla }} cm
                </b-col>
              </b-row>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">peso:</span>
                </b-col>
                <b-col cols="7">
                  {{ userData.triaje.peso }} Kg.
                </b-col>
              </b-row>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">icm</span>
                </b-col>
                <b-col cols="7">
                  {{ userData.triaje.icm }}
                </b-col>
              </b-row>

            </b-card-body>
          </b-card>
        </b-col>

        <!-- datos del especialista -->
        <b-col
          lg="4"
          md="6"
          sm="12"
        >

          <b-card
            no-body
          >
            <b-card-header class="pb-0 d-flex justify-content-center">
              <b-card-title>
                <b-badge
                  variant="light-warning"
                >
                  RESPONSABLE
                </b-badge>
              </b-card-title>
            </b-card-header>

            <b-card-body>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">dni:</span>
                </b-col>
                <b-col cols="7">
                  {{ userData.persona.numero_documento }}
                </b-col>
              </b-row>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">apellidos:</span>
                </b-col>
                <b-col cols="7">
                  {{ userData.persona.apellidos }}
                </b-col>
              </b-row>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">nombres:</span>
                </b-col>
                <b-col cols="7">
                  {{ userData.persona.nombres }}
                </b-col>
              </b-row>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">especialidad:</span>
                </b-col>
                <b-col cols="7">
                  {{ showEdad }}
                </b-col>
              </b-row>

              <b-row>
                <b-col cols="5">
                  <span class="font-weight-bolder text-capitalize">colegiatura:</span>
                </b-col>
                <b-col cols="7">
                  {{ userData.persona.sexo_nombre }}
                </b-col>
              </b-row>

            </b-card-body>
          </b-card>
        </b-col>

        <b-col
          v-if="userData.servicio.nombre === 'Odontología'"
          cols="12"
        >
          <b-card
            class="text-center"
          >
            <b-row>
              <b-list-group horizontal="md">
                <b-list-group-item style="border-radius:0px;border: none; border-bottom: 3px solid green">
                  <strong class="text-primary">{{ odontogramaNombre }}</strong>
                </b-list-group-item>
                <b-list-group-item
                  style="border: none; "
                  class="btn"
                  @click="showModalListarOdontogramas"
                >
                  ver odontogramas paciente
                </b-list-group-item>
              </b-list-group>
            </b-row>
            <br>
            <user-odontograma
              :id-paciente="idPaciente"
              :paciente="userData.persona.apellido_paterno + ' ' + userData.persona.apellido_materno + ' ' + userData.persona.nombres"
            />

            <!--             <b-tabs>
              <b-tab
                :title="odontogramaNombre"
                active
              >
                <b-card-text />
              </b-tab>
              <b-tab
                title="Historia Odontograma"
              >
                <b-card-text>
                  Historial de odontogramas
                  <historial-odontograma :id-paciente="idPaciente" />
                </b-card-text>
              </b-tab>
            </b-tabs> -->
          </b-card>
        </b-col>

        <b-col lg="6">
          <atencion-diagnostico
            :diagnosticos="userData.diagnosticos"
          />
          <!-- <pre>{{ userData.diagnosticos }}</pre> -->
        </b-col>

        <b-col lg="6">
          <atencion-procedimiento
            :procedimientos="userData.procedimientos"
          />
        </b-col>

        <b-col lg="6">
          <b-card>
            <label for="observaciones">Observaciones</label>
            <b-form-textarea
              v-model="userData.observacion"
              placeholder="Observaciones"
              rows="3"
              max-rows="6"
            />
          </b-card>
        </b-col>

        <b-col lg="6">
          <b-card>
            <div class="d-flex justify-content-around align-items-center">
              <b-button
                v-ripple.400="'rgba(186, 191, 199, 0.15)'"
                variant="primary"
                @click="showDerivarModal"
              >
                Realizar interconsulta
              </b-button>
              <b-button
                v-ripple.400="'rgba(186, 191, 199, 0.15)'"
                variant="primary"
                class="mr-25 ml-25"
                @click="$bvModal.show('modal_receta')"
              >
                Recetar
              </b-button>
              <b-button
                v-ripple.400="'rgba(186, 191, 199, 0.15)'"
                variant="primary"
                class="mr-25 ml-25"
                @click="showExamenesModal"
              >
                Examenes médicos
              </b-button>
              <b-button
                v-ripple.400="'rgba(186, 191, 199, 0.15)'"
                variant="primary"
                @click="finalizarAtencion"
              >
                Finalizar atención
              </b-button>
            </div>
          </b-card>
        </b-col>
        <user-view-user-receta
          :paciente="userData.persona.apellido_paterno + ' ' + userData.persona.apellido_materno + ' ' + userData.persona.nombres"
          :pac-documento-identidad="userData.persona.numero_documento"
        />
      </b-row>

      <!-- <pre>{{ userData }}</pre> -->

      <atencion-modal-derivar
        v-model="shallShowEmailComposeModal"
        :edited-item="editedItem"
        @actualizar-servicios="serviciosActualizados"
      />
      <atencion-modal-diagnostico @actualizar-diagnosticos-atencion="(val) => userData.diagnosticos = val" />
      <atencion-modal-procedimiento @actualizar-procedimientos-atencion="(val) => userData.procedimientos = val" />
      <modal-listar-odontograma
        :id-paciente="idPaciente"
        @cargarOdontogramaSeleccionado="cargarOdontogramaSeleccionado"
      />

      <atencion-modal-examenes
        v-model="shallShowExamenesModal"
        :edited-item="editedItem"
      />

    </section>
  </div>
</template>

<script>
import { ref, onUnmounted } from '@vue/composition-api'

import store from '@/store'
import router from '@/router'
import {
  BRow, BCol, BCard, BCardHeader, BCardBody, BCardTitle, BAlert, BLink, BFormTextarea, BBadge, BDropdown, BDropdownItem, BButton,
  BListGroup, BListGroupItem,
} from 'bootstrap-vue'
// import vSelect from 'vue-select'
import Ripple from 'vue-ripple-directive'
import { parse, differenceInHours } from 'date-fns'
import UserViewUserReceta from '@/views/salud/atencion/atencion-receta/AtencionReceta.vue'
import { useToast } from 'vue-toastification/composition'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import userStoreModule from '../userStoreModule'

import UserOdontograma from './userOdontograma.vue'
import AtencionDiagnostico from './AtencionDiagnostico.vue'
import AtencionProcedimiento from './AtencionProcedimiento.vue'
import AtencionModalDiagnostico from './AtencionModalDiagnostico.vue'
import AtencionModalProcedimiento from './AtencionModalProcedimiento.vue'
import AtencionModalDerivar from './AtencionModalDerivar.vue'
import ModalListarOdontograma from './ModalListarOdontograma.vue'
import AtencionModalExamenes from './AtencionModalExamenes.vue'

export default {
  components: {
    BDropdown,
    BDropdownItem,
    BCard,
    BCardHeader,
    BCardBody,
    BRow,
    BCol,
    BAlert,
    BLink,
    BFormTextarea,
    BBadge,
    BListGroup,
    BListGroupItem,
    BButton,
    BCardTitle,
    /* BTabs,
    BCardText,
    BTab, */

    // externos
    // vSelect,
    // BAlert,
    // BLink,
    UserViewUserReceta,
    UserOdontograma,
    AtencionDiagnostico,
    AtencionProcedimiento,
    AtencionModalDiagnostico,
    AtencionModalProcedimiento,
    AtencionModalDerivar,
    ModalListarOdontograma,
    AtencionModalExamenes,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      odontogramaNombre: 'Odontograma X',
      selected: { title: 'Presencial' },
      option: [{ title: 'Presencial' }, { title: 'Llamada telefónica' }, { title: 'Videollamada' }, { title: 'WhatsApp' }],
      dateNtim: null,
    }
  },
  computed: {
    showEdad() {
      const anio = this.userData.persona.fecha_nacimiento.substring(0, 4)
      const mes = this.userData.persona.fecha_nacimiento.substring(5, 7)
      const dia = this.userData.persona.fecha_nacimiento.substring(8, 10)
      const fechaNacimiento = parse(`${dia}/${mes}/${anio}`, 'dd/MM/yyyy', new Date())
      const horas = differenceInHours(new Date(), fechaNacimiento)
      return Math.trunc(horas / 8760)
    },
  },
  methods: {
    generarNuevoOdontograma(idPaciente) {
      this.$swal({
        title: '¿Estas seguro?',
        text: '',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Sí, crear nuevo odontograma!',
        customClass: {
          confirmButton: 'btn btn-success',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          console.log('nuevo dodontohrama en proceso!!', idPaciente)
          this.$emit('nuevoOdontograma', idPaciente)
        } else if (result.dismiss === 'cancel') {
          this.$swal({
            title: 'Cancelado',
            text: 'Tu odontograma actual está seguro :)',
            icon: 'error',
            customClass: {
              confirmButton: 'btn btn-success',
            },
          })
        }
      })
    },
    showModalListarOdontogramas() {
      this.$emit('listarOdontogramas')
      this.$bvModal.show('modal_listar_odontograma')
    },
    cargarOdontogramaSeleccionado(dataOdontograma) {
      this.$emit('cargarOdontograma', dataOdontograma)
    },
  },

  setup(_, { emit }) {
    const userData = ref(null)
    const idPaciente = ref(null)
    const editedItem = ref({})
    const errorMessage = ref(null)
    const shallShowEmailComposeModal = ref(false)
    const shallShowExamenesModal = ref(false)

    const ATENCION_APP_STORE_MODULE_NAME = 'app-atencion'
    const toast = useToast()

    // Register module
    if (!store.hasModule(ATENCION_APP_STORE_MODULE_NAME)) store.registerModule(ATENCION_APP_STORE_MODULE_NAME, userStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(ATENCION_APP_STORE_MODULE_NAME)) store.unregisterModule(ATENCION_APP_STORE_MODULE_NAME)
    })

    const showDerivarModal = () => {
      editedItem.value = { ...userData.value }
      shallShowEmailComposeModal.value = true
      emit('update:shall-show-email-compose-modal', true)
    }

    const showExamenesModal = () => {
      editedItem.value = { ...userData.value }
      shallShowExamenesModal.value = true
      emit('update:shall-show-examenes-modal', true)
    }

    store.dispatch('app-atencion/fetchAtencion', { id: router.currentRoute.params.id })
      .then(response => {
        idPaciente.value = response.data.paciente_id
        const { usuario, ...rest } = response.data
        const { perfil } = usuario
        const { persona } = perfil
        userData.value = { ...rest, persona }
      })
      .catch(error => {
        if (error.response.status === 404) {
          errorMessage.value = 'Error al obtener los datos de la atencion'
          userData.value = undefined
        } else if (error.response.status === 406) {
          errorMessage.value = error.response.data.error
          userData.value = undefined
        }
      })

    const serviciosActualizados = item => {
      userData.value.servicios = item
    }

    const finalizarAtencion = () => {
      store.dispatch('app-atencion/finalizarAtencion', userData.value)
        .then(() => {
          toast({
            component: ToastificationContent,
            props: {
              title: 'Finalización exitosa!',
              icon: 'AlertTriangleIcon',
              variant: 'success',
            },
          })
          router.push({ name: 'salud-atencion-list' })
        })
        .catch(error => {
          toast({
            component: ToastificationContent,
            props: {
              title: 'Error!',
              icon: 'EditIcon',
              text: `${error.response.data.error}`,
              variant: 'danger',
            },
          })
        })
    }

    return {
      userData,
      idPaciente,
      errorMessage,
      finalizarAtencion,
      showDerivarModal,
      shallShowEmailComposeModal,
      editedItem,
      serviciosActualizados,
      shallShowExamenesModal,
      showExamenesModal,
    }
  },
}
</script>

<style lang="scss">
@import '@core/scss/vue/libs/vue-flatpicker.scss';
@import '@core/scss/vue/libs/vue-select.scss';
</style>
